
body {
  font-family: 'Microsoft YaHei';
  background-color: #faf8ef;
}
body .menu-container {
  width: 300px;
  margin: 0 auto;
  text-align: right;
}
body .block {
  display: inline-block;
  padding: 3px 12px;
  height: 2;
  text-align: center;
  background-color: #8f7a66;
  color: #f9f6f2;
  border-radius: 6px;
  font-size: 18px;
  margin: 5px;
}
body .score-container .block {
  color: rgba(255, 255, 255, 0.6);
  font-size: 14px;
  width: 48px;
  background-color: rgba(143, 122, 102, 0.8);
}
body .score-container .block span {
  display: block;
  color: white;
}
body .restart {
  background-color: #d4430f;
}
body .restart:active {
  opacity: 0.8;
}

.Custom2048 canvas {
  position: absolute;
  top: 100px;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  display: block;
  border-radius: 6px;
}
